import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";

const card = [
  {
    title: "  ",
    span: "    ,  ",
    txt: [
      "TMMi     ",
      "KDLAS   ",
    ],
    keywords: ["SQA", " ", " ", "Test"],
  },
  {
    title: " ",
    span: "     ",
    txt: [
      "     'TestFort'",
      "    IMQA",
    ],
    keywords: [
      "  ",
      "IMQA",
      "TestFort",
      "   ",
    ],
  },
  {
    title: " ",
    span: "     ",
    txt: [
      "     'Ankus'",
      "Library, Analyzer, Optimizer,  ",
    ],
    keywords: ["  ", "  ", " ", ""],
  },
];
const BusinessBox = () => {
  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <>
      {card.map((item, index) => (
        <div
          className="content-inner"
          data-aos="fade-up"
          data-aos-duration="800"
          data-aos-delay="200"
          data-aos-easing="ease-in-out"
        >
          <div className="content-box" key={index}>
            <div className="text-box">
              <h3>{item.title}</h3>
              <span>{item.span}</span>
              <p>{item.txt[0]}</p>
              <p>{item.txt[1]}</p>
              <p>{item.txt[2] ? item.txt[2] : null}</p>
            </div>
            <div className="keywords-box">
              {item.keywords?.map((k, index) => (
                <p key={index}>{k}</p>
              ))}
            </div>
          </div>
          <div className="top"></div>
        </div>
      ))}
    </>
  );
};

export default BusinessBox;
